
.page {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
}
.page2 {
  background-image: url("../assets/bg_second.png");
}
.page2 .layer{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.page2 img{
   width: 100%;
   height: auto; 
   margin: 0 auto;
    display: block;
   pointer-events: all
}
svg{
  fill: none;
}
.page2 .main {
  height:463px;
  margin-top: 120px;
  animation: easeIn 2s ease-in;
}
.page2 .title {
   animation: bounceIn 1s linear;
}
.page2 .main .cardbg{
   width: 350px;
   margin: 0 auto;
}
.page2 .main .grid {
  width: 350px;
  height: 280px;
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translate(-50%, 0);
}
.page2 .button img{
  width: 223px;
  margin: 490px auto 0 auto; 
  pointer-events: all
}
.col {
  width: 33.3%;
  height: 100%;
  box-sizing: border-box;
  padding: 0 10px;
  float: left;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  position: relative;
}
.first {
}
.avatar {
  width: 100%;
  height: auto;
  transition: all 0.5s;
}
.avatar:active {
  transform: scale(1.2);
}
.line2 {
  stroke-dashoffset: 30;
}
.lineBox1 .line1 {
   stroke-dasharray: 6;
}
.lineBox3 .line3 {
  stroke-dasharray: 6;
}
.line1, .line3{
  stroke-dashoffset: 70;
}
.lineOverBox {
  position: absolute;
  top:0;
  left: 0;  
  transform: rotate(180deg);
}
.lineOver {
  stroke: #fff;
  stroke-width: 6;
  stroke-dashoffset: 0;
  opacity: 0;
}
.lineBox2 .lineOver {
  stroke-dasharray: 48;
  animation: dash48 0.7s linear;
}
.lineBox1 .lineOver, .lineBox3 .lineOver {
  stroke-dasharray: 70;
  animation: dash70 0.7s linear;
}
.lineBox3 {
  transform: rotate(65deg);
  transform-origin: left center;
  width:100px;
  top:60%;
  left:89%;
}
.lineBox1 {
  transform: rotate(-65deg);
  transform-origin: left center;
  width:100px;
  top:60%;
  left:89%;
}
.lineBox2 {
  transform: rotate(0deg);
  transform-origin: left center;
  width:48px;
  top:60%;
  left:89%;
}
.lineBox1-0 {
}
.lineBox1-1 {
}
.lineBox1-2 {
}
.lineBox1-3 {
}
.lineBox1-4 {
}
.lineBox1-5 {
}

/* row1  */
.lineBoxRow1{
    left: 69%;
    transform: rotate(35deg);
    transform-origin: left center;
    width: 70px;
    top: 30%;
}
.lineBoxRow1 .lineOver {
  stroke-dasharray: 70;
  animation: dash70 0.7s linear;
  stroke-dashoffset: 0;
  opacity: 0;
}
.lineBoxRow1 .line{
    stroke-dasharray: 6;
}

/* row1 end  */

/* row2  */
