.lineWarpper {
   position: absolute;
   height: 4px;
   /* height: 4px;
   width: 70px; */
}
.lineBox{
  width: 100%;
  height: 100%;
  display: block;
}
.line {
  stroke: #09d;
  stroke-width: 4;
  stroke-linecap: round;
  stroke-dasharray: 8;
}
@keyframes dash48 {
from {
   opacity: 1;
    stroke-dashoffset: 0;
}
to {
    opacity: 1;
    stroke-dashoffset: 48;
}
}
@keyframes dash70 {
from {
   opacity: 1;
    stroke-dashoffset: 0;
}
to {
    opacity: 1;
    stroke-dashoffset: 70;
}
}
.lastCol {
  padding: 60px 25px 60px 0px;
}
.shake {
  animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
}




