
.page3 {
  overflow-y:scroll;
  background-image: url("../assets/bg_repeat.png");
  background-size: contain;
  background-repeat: repeat-y;
}
.page3 .result {
  width: 100%;
  max-width: 357px;
  display: block;
  margin: 30px auto 0 auto;
}
.page3 .result img{
  width: 100%;
  height: auto;
  display: block;
}
.page3 .btns{
  text-align: center;
  margin-top: 30px;
}
.page3 .btns .btn{
  display: inline-block;
  width: 181px;
}




