
#app {
  width:100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.fade-enter-active, .fade-leave-active {
  transition: all 2s;
}
.fade-leave {
  opacity: 1;
}
.fade-leave-to {
 opacity: 0;
}
.fade-enter {
  opacity: 0;
}
.fade-enter-to {
  opacity: 1;
}



